@font-face {
	font-family: 'icomoon';
	src:  url('../fonts/icomoon.eot?mok0eb');
	src:  url('../fonts/icomoon.eot?mok0eb#iefix') format('embedded-opentype'),
	url('../fonts/icomoon.ttf?mok0eb') format('truetype'),
	url('../fonts/icomoon.woff?mok0eb') format('woff'),
	url('../fonts/icomoon.svg?mok0eb#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

